.greeting {
  position: relative;
  padding-top: 70px;
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center -19px;
  background-size: 575px 373px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
  }
}

.greeting__slogan {
  width: 260px;
  margin: 0 auto;
  margin-bottom: 122px;
  padding: 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  text-align: center;
  word-break: break-word;
}

.greeting__wrap {
  width: 100%;
}

@media (min-width: $table-width) {
  .greeting {
    padding-top: 0;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: center -1px;
    background-size: 1150px 746px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
    }

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      bottom: -10px;
      right: 0;
      height: 34px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
      background-position: center bottom;
      background-size: contain;
    }
  }

  .greeting__wrap {
    width: calc(100% - 100px);
    margin: 0 auto;
    padding-top: 152px;
  }

  .greeting__slogan {
    margin-bottom: 374px;
    width: 500px;
    font-size: $title-size-desk;
    line-height: $title-height-desk;
  }
}

@media (min-width: $desktop-width) {
  .greeting__slogan {
    font-size: 53px;
    line-height: 57px;
    width: 717px;
    margin-bottom: 360px;
  }

  .greeting {
    padding-top: 0;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-size: 1150px auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }

    &::after {
      content: "";
      width: 100%;
      bottom: -10px;
      right: 0;
      height: 35px;
      background-image: url("../img/bg-zigzag-fill.des.svg");
      background-size: 1250px 34px;
    }
  }
}
